import React from 'react';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';

const NoTemplates = props => {
	return (
		<div className="box has-text-centered no-templates">
			<FontAwesomeIcon icon="clipboard-list" size={'7x'} />
			<p>
				You still haven't created your first template yet.{' '}
				<strong>Get Started</strong>
			</p>
			<div className="button" onClick={props.openModal}>
				+ CREATE TEMPLATE
			</div>
		</div>
	);
};

export default NoTemplates;



