import React from 'react';
import { NavLink } from 'react-router-dom';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';

const Sidebar = () => {
  return (
    <aside className="menu">
      <p className="menu-label">Conversations</p>
      <ul className="menu-list">
        <li>
          <NavLink activeClassName="is-active" to="/inbox">
            <FontAwesomeIcon icon="inbox" size={'2x'} />
            Inbox
          </NavLink>
        </li>
        <li>
          <NavLink activeClassName="is-active" to="/requests">
            <FontAwesomeIcon icon="exchange-alt" size={'2x'} />
            Requests
          </NavLink>
        </li>
        {/*<li>
          <NavLink to="/">Home</NavLink>
        </li>*/}
      </ul>
      <p className="menu-label">Templates</p>
      <ul className="menu-list">
        <li>
          <NavLink to="/template" activeClassName="is-active">
            <FontAwesomeIcon icon="file-alt" size={'2x'} /> Templates
          </NavLink>
        </li>
      </ul>
      <p className="menu-label">Contracts</p>
      <ul className="menu-list">
        <li>
          <NavLink exact to="/assign-machine" activeClassName="is-active">
            <FontAwesomeIcon icon="cog" size={'2x'} /> Assign Machine
          </NavLink>
        </li>
        <li>
          <NavLink exact to="/manage-contracts" activeClassName="is-active">
            <FontAwesomeIcon icon="code-branch" size={'2x'} /> Manage Contracts
          </NavLink>
        </li>
      </ul>
    </aside>
  );
};

export default Sidebar;



