export default (state = [], action) => {
	switch (action.type) {
		case 'FETCH_TEMPLATES':
			return action.payload;
		case 'DELETE_TEMPLATE':
			return state.filter(template => template.id !== action.payload);
		case 'ADD_TEMPLATE':
			return [...state, action.payload];
		default:
			return state;
	}
};



