import React from 'react';
import { reduxForm, Field, reset } from 'redux-form';
import { connect } from 'react-redux';

import RenderField from '../../FormElements/RenderField';
import logicalFields from '../logicalFields';
import { required } from '../../../validations';
import { getConstant, updateConstant, deleteConstant } from '../../../actions';

import Accordion from './Accordion';

let Variable = props => {
	const logicalSection = logicalFields[5];

	const isUpdate =
		Object.keys(props.initialValues).length === 0 &&
		props.initialValues.constructor === Object
			? false
			: true;

	return (
		<Accordion
			{...props}
			index={0}
			list="constant"
			label={logicalSection.label}
			type={logicalSection.type}
			onAddVariableClick={() => props.getConstant(props.state.activeTemplateId)}
			deleteVariable={props.deleteConstant}
			onEdit={props.onEdit}
			onUpdate={props.handleSubmit(async values => {
				values.template_id = props.state.activeTemplateId;
				values.user_id = props.state.userId;
				const res = await props.updateConstant(values, isUpdate);
				return res;
			})}
		>
			<form
				onSubmit={props.handleSubmit(values => {
					values.template_id = props.state.activeTemplateId;
					values.user_id = props.state.userId;
					props.updateConstant(values, isUpdate);
				})}
			>
				{logicalSection.inputs.map(singleField => (
					<Field
						key={singleField.name}
						component={RenderField}
						type={singleField.type}
						name={singleField.name}
						label={singleField.label}
						placeHolder={singleField.placeHolder}
						validate={required}
						selectOptions={singleField.options}
					/>
				))}
				{/*<button className="button is-primary">Submit</button>*/}
			</form>
		</Accordion>
	);
};

const clearValues = (result, dispatch) => dispatch(reset('constantForm'));

Variable = reduxForm({
	form: 'constantForm',
	enableReinitialize: true,
	destroyOnUnmount: true,
	onSubmitSuccess: clearValues
})(Variable);

const mapDispatchToProps = dispatch => ({
	updateConstant: (values, isUpdate) =>
		dispatch(updateConstant(values, isUpdate)),
	getConstant: tid => dispatch(getConstant(tid)),
	deleteConstant: (id, userId) => dispatch(deleteConstant(id, userId)),
	clearValues: () => dispatch(reset('constantForm'))
});

//const mapStateToProps = state => ({ state });

const mapStateToProps = state => {
	const initialValues = state.variableFormValues || {};
	return { initialValues, state: state };
};

export default connect(
	mapStateToProps,
	mapDispatchToProps
)(Variable);



