import React from 'react';
import { reduxForm, Field, reset } from 'redux-form';
import { connect } from 'react-redux';

import RenderField from '../../FormElements/RenderField';
import logicalFields from '../logicalFields';
import { required } from '../../../validations';
import { getTrigger, updateTrigger, deleteTrigger } from '../../../actions';

import Accordion from './Accordion';

let Trigger = props => {
	const logicalSection = logicalFields[4];
	const isUpdate =
		Object.keys(props.initialValues).length === 0 &&
		props.initialValues.constructor === Object
			? false
			: true;
	return (
		<Accordion
			{...props}
			index={0}
			list="trigger"
			label={logicalSection.label}
			type={logicalSection.type}
			onAddVariableClick={props.getTrigger}
			deleteVariable={props.deleteTrigger}
			onEdit={props.onEdit}
			onUpdate={props.handleSubmit(async values => {
				values.template_id = props.state.activeTemplateId;
				values.user_id = props.state.userId;
				const res = await props.updateTrigger(values, isUpdate);
				return res;
			})}
		>
			<form
				onSubmit={props.handleSubmit(values => {
					values.template_id = props.state.activeTemplateId;
					values.user_id = props.state.userId;
					props.updateTrigger(values, isUpdate);
				})}
			>
				{logicalSection.inputs.map(singleField => (
					<Field
						key={singleField.name}
						component={RenderField}
						type={singleField.type}
						name={singleField.name}
						label={singleField.label}
						placeHolder={singleField.placeHolder}
						validate={required}
					/>
				))}
				{/*<button className="button is-primary">Submit</button>*/}
			</form>
		</Accordion>
	);
};

const clearValues = (result, dispatch) => {
	return dispatch(reset('triggerForm'));
};

Trigger = reduxForm({
	form: 'triggerForm',
	enableReinitialize: true,
	destroyOnUnmount: true,
	onSubmitSuccess: clearValues
})(Trigger);

const mapDispatchToProps = dispatch => ({
	updateTrigger: (values, isUpdate) =>
		dispatch(updateTrigger(values, isUpdate)),
	getTrigger: () => dispatch(getTrigger()),
	deleteTrigger: (id, userId) => dispatch(deleteTrigger(id, userId)),
	clearValues: () => dispatch(reset('triggerForm'))
});

//const mapStateToProps = state => ({ state });

const mapStateToProps = state => {
	const initialValues = state.variableFormValues || {};
	return { initialValues, state: state };
};

export default connect(
	mapStateToProps,
	mapDispatchToProps
)(Trigger);



