import React, { Component } from 'react';
import { connect } from 'react-redux';
import { BrowserRouter, Route } from 'react-router-dom';
import 'bulma/css/bulma.css';
import './index.css';
import Navbar from './components/Navbar';
import axios from 'axios';

import { library } from '@fortawesome/fontawesome-svg-core';

//prettier-ignore
import { faInfoCircle, faStroopwafel, faInbox, faExchangeAlt, faFileAlt, faCog, faCodeBranch, faClipboardList, faEllipsisH, faChevronDown, faChevronUp, faChevronRight, faUserCircle, faSearch, faCheck, faExclamationTriangle, faPen } from '@fortawesome/free-solid-svg-icons';

import Header from './components/Header';
import Sidebar from './components/Sidebar';
import Template from './components/templates/Template';
import Inbox from './components/Inbox';
import Requests from './components/Requests';
import AssignMachine from './components/AssignMachine';
import ManageContracts from './components/ManageContracts';

import Alert from 'react-s-alert';
import 'react-s-alert/dist/s-alert-default.css';
import 'react-s-alert/dist/s-alert-css-effects/slide.css';

import {
  simpleAction,
  fetchTemplates,
  deleteTemplate,
  addTemplate,
  updateUserId
} from './actions';

//prettier-ignore
library.add( faInfoCircle, faStroopwafel, faInbox, faExchangeAlt, faFileAlt, faCog, faCodeBranch, faClipboardList, faEllipsisH, faChevronDown, faChevronUp, faChevronRight, faUserCircle, faSearch, faCheck, faExclamationTriangle, faPen);

const details = {
  email: localStorage.email || prompt('Enter email'),
  password: localStorage.password || prompt('Enter password')
};

class App extends Component {
  state = {
    templateList: null
  };
  componentDidMount() {
    this.doLogin().then(user => {
      this.props.updateUserId(user.id);
      this.props.fetchTemplates();
    });
  }

  doLogin = async () => {
    const { data } = await axios.post('/login', details);
    if (!data.error) {
      localStorage.email = details.email;
      localStorage.password = details.password;
    }
    return data.user;
  };

  fetchTemplates = () => {
    return fetch('/api/templates').then(response => response.json());
  };

  simpleAction = event => {
    this.props.simpleAction();
  };

  render() {
    const loading = this.props.pageLoading ? 'is-loading' : '';
    return (
      <BrowserRouter>
        <div>
          <Navbar />
          <div className="columns app">
            <div className="column is-2 sidebar">
              <Sidebar />
            </div>
            <div className="column is-10 main-body">
              <Route exact path="/" component={Header} />
              {this.props.templates && (
                <Route
                  path="/template"
                  render={({ match }) => (
                    <Template
                      templateList={this.props.templates}
                      deleteTemplate={this.props.deleteTemplate}
                      addTemplate={this.props.addTemplate}
                      match={match}
                    />
                  )}
                />
              )}
              <Route path="/inbox" render={() => <Inbox />} />
              <Route path="/requests" render={() => <Requests />} />
              <Route path="/assign-machine" render={() => <AssignMachine />} />
              <Route
                path="/manage-contracts"
                render={() => <ManageContracts />}
              />

              <Route path="/header" component={Header} />
              <div className={`pageloader ${loading}`}>
                <h3 className={`button ${loading}`}>Loading</h3>
              </div>
            </div>
            <Alert timeout={6000} />
          </div>
        </div>
      </BrowserRouter>
    );
  }
}

const mapStateToProps = state => state;

const mapDispatchToProps = dispatch => ({
  simpleAction: data => dispatch(simpleAction(data)),
  fetchTemplates: data => dispatch(fetchTemplates()),
  deleteTemplate: id => dispatch(deleteTemplate(id)),
  addTemplate: values => dispatch(addTemplate(values)),
  updateUserId: id => dispatch(updateUserId(id))
});

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(App);



