import React, { Component } from 'react';
import { connect } from 'react-redux';
import { withRouter } from 'react-router-dom';

import HeaderTab from './HeaderTab';

import Variable from './LogicalForms/Variable';
import Clause from './LogicalForms/Clause';
import Event from './LogicalForms/Event';
import Trigger from './LogicalForms/Trigger';
import Constant from './LogicalForms/Constant';

class Logical extends Component {
	state = {
		loading: false
	};

	startLoading = () => {
		this.setState({
			loading: true
		});
	};

	stopLoading = () => {
		this.setState({
			loading: false
		});
	};

	expandEditModal = children => {
		this.setState({ isEditExpanded: true, form: children });
	};

	closeEditModal = () => {
		this.setState({ isEditExpanded: false });
	};

	componentDidMount() {
		if (!this.props.state.activeTemplateId) {
			this.props.history.push('/template');
		}
	}

	render() {
		const { loading } = this.state;
		return (
			<div>
				<div className={`pageloader ${loading}`}>
					<h3 className={`button ${loading}`}>Loading</h3>
				</div>
				<HeaderTab activeTab="Logical" />
				<div>
					<Variable />
					<Clause />
					<Trigger />
					<Event />
					<Constant />
				</div>
			</div>
		);
	}
}

const mapStateToProps = state => ({ state });

export default withRouter(
	connect(
		mapStateToProps,
		null
	)(Logical)
);



