import React from 'react';

const EventTable = props => {
	const {
		tableClass,
		variablesArray,
		enableEditModal,
		openDelete,
		tableName
	} = props;
	switch (tableName) {
		case 'variable':
			return (
				<table className={`table is-striped is-fullwidth ${tableClass}`}>
					<thead>
						<tr>
							<th>Name</th>
							<th>Type</th>
							<th>Description</th>
							<th>Actions</th>
						</tr>
					</thead>
					<tbody>
						{variablesArray.map(variable => (
							<tr key={variable.id}>
								<td>{variable.name}</td>
								<td>{variable.data_type}</td>
								<td>{variable.description}</td>
								<td>
									<button
										className="button is-info is-outlined edit-variable"
										onClick={() => enableEditModal(variable)}
									>
										Edit
									</button>
									<button
										className="button is-danger is-outlined"
										onClick={() => openDelete(variable.id, variable.name)}
									>
										Remove
									</button>
								</td>
							</tr>
						))}
					</tbody>
				</table>
			);
		case 'clause':
			return (
				<table className={`table  is-fullwidth ${tableClass}`}>
					<thead>
						<tr>
							<th>Name</th>
							<th>Description</th>
							<th>ClauseLogic String</th>
							<th>Actions</th>
						</tr>
					</thead>
					<tbody>
						{variablesArray.map(variable => (
							<tr key={variable.id}>
								<td>{variable.name}</td>
								<td>{variable.description}</td>
								<td>{variable.clause_logic_string}</td>
								<td>
									<button
										className="button is-info is-outlined edit-variable"
										onClick={() => enableEditModal(variable)}
									>
										Edit
									</button>
									<button
										className="button is-danger is-outlined"
										onClick={() =>
											openDelete(variable.id, variable.clause_logic_string)
										}
									>
										Remove
									</button>
								</td>
							</tr>
						))}
					</tbody>
				</table>
			);
		case 'event':
			return (
				<table className={`table is-fullwidth ${tableClass}`}>
					<thead>
						<tr>
							<th>Event Name</th>
							<th>Trigger ID</th>
							<th>Actions</th>
						</tr>
					</thead>
					<tbody>
						{variablesArray.map(variable => (
							<tr key={variable.id}>
								<td>{variable.event_name}</td>
								<td>{variable.trigger_id}</td>
								<td>
									<button
										className="button is-info is-outlined edit-variable"
										onClick={() => enableEditModal(variable)}
									>
										Edit
									</button>
									<button
										className="button is-danger is-outlined"
										onClick={() => openDelete(variable.id, variable.event_name)}
									>
										Remove
									</button>
								</td>
							</tr>
						))}
					</tbody>
				</table>
			);
		case 'constant':
			return (
				<table className={`table is-fullwidth ${tableClass}`}>
					<thead>
						<tr>
							<th>Name</th>
							<th>Type</th>
							<th>Value</th>
							<th>Description</th>
							<th>Actions</th>
						</tr>
					</thead>
					<tbody>
						{variablesArray.map(variable => (
							<tr key={variable.id}>
								<td>{variable.name}</td>
								<td>{variable.data_type}</td>
								<td>{variable.value}</td>
								<td>{variable.description}</td>
								<td>
									<button
										className="button is-info is-outlined edit-variable"
										onClick={() => enableEditModal(variable)}
									>
										Edit
									</button>
									<button
										className="button is-danger is-outlined"
										onClick={() => openDelete(variable.id, variable.name)}
									>
										Remove
									</button>
								</td>
							</tr>
						))}
					</tbody>
				</table>
			);
		case 'trigger':
			return (
				<table className={`table is-fullwidth ${tableClass}`}>
					<thead>
						<tr>
							<th>Name</th>
							<th>ID</th>
							<th>Condition</th>
							<th>Actions</th>
						</tr>
					</thead>
					<tbody>
						{variablesArray.map(variable => (
							<tr key={variable.id}>
								<td>{variable.name}</td>
								<td>{variable.id}</td>
								<td>{variable.trigger_logic}</td>
								<td>
									<button
										className="button is-info is-outlined edit-variable"
										onClick={() => enableEditModal(variable)}
									>
										Edit
									</button>
									<button
										className="button is-danger is-outlined"
										onClick={() => openDelete(variable.id, variable.name)}
									>
										Remove
									</button>
								</td>
							</tr>
						))}
					</tbody>
				</table>
			);
	}
};

export default EventTable;



