export default [
	{
		label: 'Define a Variable',
		type: 'variable',
		formName: 'variableForm',
		inputs: [
			{
				label: 'Name of the variable',
				placeHolder: 'Enter name of the variable',
				name: 'name',
				type: 'input'
			},
			{
				label: 'Type of variable',
				placeHolder: 'Enter name of the variable',
				name: 'data_type',
				type: 'select',
				options: [
					'Choose type of variable',
					'Number',
					'Yes / No',
					'Text',
					'Decimal Number',
					'Date'
				]
			},
			{
				label: 'Description',
				placeHolder: 'Choose the type of the variable',
				name: 'description',
				type: 'textarea'
			}
		]
	},
	{
		label: 'Define a Clause',
		type: 'clause',
		formName: 'clauseForm',
		inputs: [
			{
				label: 'Name of the Clause',
				placeHolder: 'Enter name of the Clause',
				name: 'name',
				type: 'input'
			},
			{
				label: 'Description',
				placeHolder: 'Enter Description',
				name: 'description',
				type: 'input'
			},
			{
				label: 'ClauseLogic String',
				placeHolder: 'Enter ClauseLogic String',
				name: 'clause_logic_string',
				type: 'textarea'
			}
		]
	},
	{
		label: 'Define an Event',
		type: 'event',
		formName: 'eventForm',
		inputs: [
			{
				label: 'Name of the Event',
				placeHolder: 'Enter name of the Event',
				name: 'event_name',
				type: 'input'
			}
		]
	},
	{
		label: 'Define a Parameter',
		type: 'parameter',
		formName: 'parameterForm',
		inputs: [
			{
				label: 'Name of the Parameter',
				placeHolder: 'Enter name of the parameter',
				name: 'parameter_name',
				type: 'input'
			}
		]
	},
	{
		label: 'Define a Trigger',
		type: 'trigger',
		formName: 'triggerForm',
		inputs: [
			{
				label: 'Name of the Trigger',
				placeHolder: 'Enter name of trigger',
				name: 'name',
				type: 'input'
			},
			{
				label: 'Condition',
				placeHolder: 'Enter Condition',
				name: 'trigger_logic',
				type: 'input'
			}
		]
	},
	{
		label: 'Define a Constant',
		type: 'constant',
		formName: 'constantForm',
		inputs: [
			{
				label: 'Name of the Constant',
				placeHolder: 'Enter name of the Constant',
				name: 'name',
				type: 'input'
			},
			{
				label: 'Type of variable',
				placeHolder: 'Enter name of the variable',
				name: 'data_type',
				type: 'select',
				options: [
					'Choose type of variable',
					'Number',
					'Yes / No',
					'Text',
					'Decimal Number',
					'Date'
				]
			},
			{
				label: 'Value of the Constant',
				placeHolder: 'Enter the value',
				name: 'value',
				type: 'input'
			},
			{
				label: 'Description of the Constant',
				placeHolder: 'Enter Description of the Constant',
				name: 'description',
				type: 'textarea'
			}
		]
	}
];



