import { combineReducers } from 'redux';
import simpleReducer from './simpleReducer';
import templatesReducer from './templatesReducer';
import activeTemplateReducer from './activeTemplateReducer';
import activeTextualDataReducer from './activeTextualDataReducer';
import userIdReducer from './userIdReducer';
import pageLoadingReducer from './pageLoadingReducer';
import variableFormReducer from './variableFormReducer';
import clauseFormReducer from './clauseFormReducer';
import eventFormReducer from './eventFormReducer';
import parameterFormReducer from './parameterFormReducer';
import triggerFormReducer from './triggerFormReducer';
import constantFormReducer from './constantFormReducer';

import variableListReducer from './variableListReducer';
import clauseListReducer from './clauseListReducer';
import constantListReducer from './constantListReducer';
import eventListReducer from './eventListReducer';
import triggerListReducer from './triggerListReducer';

import { reducer as reduxForm } from 'redux-form';
export default combineReducers({
	simpleReducer,
	templates: templatesReducer,
	form: reduxForm,
	activeTemplateId: activeTemplateReducer,
	activeTextualData: activeTextualDataReducer,
	userId: userIdReducer,
	pageLoading: pageLoadingReducer,
	variableFormValues: variableFormReducer,
	clauseFormValues: clauseFormReducer,
	eventFormValues: eventFormReducer,
	parameterFormValues: parameterFormReducer,
	triggerFormValues: triggerFormReducer,
	constantFormValues: constantFormReducer,
	variableList: variableListReducer,
	clauseList: clauseListReducer,
	constantList: constantListReducer,
	eventList: eventListReducer,
	triggerList: triggerListReducer
});



