export default (state = [], action) => {
	switch (action.type) {
		case "FETCH_TRIGGER_LIST":
			return action.payload;
		case "ADD_TO_TRIGGER_LIST":
			return [...state, action.payload];
		case "REMOVE_FROM_TRIGGER_LIST":
			return state.filter(variable => variable.id !== action.payload);
		case "UPDATE_TO_TRIGGER_LIST":
			const index = state.findIndex(
				variable => variable.id === action.payload.id
			);
			let updatedState = state;
			updatedState[index] = action.payload;
			return JSON.parse(JSON.stringify(updatedState));
		default:
			return state;
	}
};



