import React, { Component } from 'react';
import { reduxForm } from 'redux-form';
import {
	updateClause,
	deleteVariable,
	updateVariableFormValues,
	clearFormValues
} from '../../../actions';
import { connect } from 'react-redux';

import {
	Accordion,
	AccordionItem,
	AccordionItemTitle,
	AccordionItemBody
} from 'react-accessible-accordion';

import VariableTable from './VariableTables/VariableTable';

import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import 'react-accessible-accordion/dist/fancy-example.css';

const EditModal = props => {
	return (
		<div className={`modal is-active new-template-modal`}>
			<div className="modal-background" />
			<div className="modal-content">
				<p className="modal-header">
					<strong>Edit {props.variableName}</strong>
				</p>
				{props.children}
				<button
					className="modal-close is-large"
					aria-label="close"
					onClick={props.closeEditModal}
				/>
			</div>
		</div>
	);
};

class AccordionSection extends Component {
	state = {
		expanded: false,
		tableClass: true,
		deleteVariableId: null,
		deleteVariableName: null,
		showDeleteModal: false,
		noVariables: true,
		variablesExist: false,
		showForm: false,
		editModalActive: false,
		loading: true,
		variablesArrayLength: 0
	};

	enableEditModal = values => {
		this.setState({
			editModalActive: true
		});
		this.closeForm();
		this.props.updateVariableFormValues(values);
	};

	closeEditModal = () => {
		this.setState({
			editModalActive: false
		});
	};

	toggleExpand = e => {
		if (this.state.expanded) {
			this.setState({ expanded: !this.state.expanded, showForm: false });
		} else {
			this.setState({ expanded: !this.state.expanded });
			this.props.onAddVariableClick().then(() => {
				this.setState({ loading: false });
			});
		}
		if (!this.state.variablesExist) {
			this.setState({
				noVariables: true
			});
		}
		this.props.clearValues();
	};

	removeNoVariable = () => {
		this.setState({
			noVariables: false
		});
	};

	onSave = async e => {
		e.preventDefault();
		e.stopPropagation();
		await this.props.onSave();
		this.setState({
			showForm: false
		});
		this.closeForm();
	};

	closeForm = () => {
		this.props.clearFormValues(this.props.list);
		this.setState({
			showForm: false
		});
		if (!this.state.variablesExist) {
			this.setState({
				noVariables: true
			});
		}
	};

	toggleTable = () => {
		this.setState({
			tableClass: !this.state.tableClass
		});
	};

	closeDelete = () => {
		this.setState({ showDeleteModal: false });
	};

	openDelete = (id, name) => {
		this.setState({
			deleteVariableId: id,
			deleteVariableName: name,
			showDeleteModal: true
		});
	};

	submitDelete = () => {
		this.props
			.deleteVariable(this.state.deleteVariableId, this.props.state.userId)
			.then(() => {
				this.setState({
					showDeleteModal: false,
					showForm: false
				});
			});
	};

	showForm = e => {
		e.preventDefault();
		e.stopPropagation();
		this.setState({
			showForm: true
		});
		this.removeNoVariable();
		this.props.clearValues();
		this.props.clearFormValues(this.props.list);
	};

	renderVariableList = variablesArray => {
		const tableClass = this.state.tableClass ? null : 'is-hidden';

		variablesArray = variablesArray.filter(variable => {
			return variable.template_id === this.props.state.activeTemplateId;
		});

		if (!variablesArray.length) return;

		variablesArray.sort(function(a, b) {
			return new Date(b.updatedAt) - new Date(a.updatedAt);
		});

		return (
			<VariableTable
				tableName={this.props.list}
				tableClass={tableClass}
				variablesArray={variablesArray}
				enableEditModal={this.enableEditModal}
				openDelete={this.openDelete}
			/>
		);
	};

	componentDidUpdate() {
		const variablesExist = Boolean(
			this.props.state[this.props.list + 'List'].filter(
				variable => variable.template_id === this.props.state.activeTemplateId
			).length
		);

		if (variablesExist && !this.state.variablesExist) {
			this.setState({ variablesExist: true, tableClass: true });
			this.removeNoVariable();
		}

		if (!variablesExist && this.state.variablesExist) {
			this.setState({
				variablesExist: false,
				tableClass: false,
				noVariables: true
			});
		}

		const variablesArrayLength = this.props.state[
			this.props.list + 'List'
		].filter(
			variable => variable.template_id === this.props.state.activeTemplateId
		).length;
	}

	render() {
		const arrowIcon = this.state.expanded ? 'chevron-up' : 'chevron-down';
		const showDeleteModal = this.state.showDeleteModal ? 'is-active' : '';
		const loading = this.state.loading ? 'is-loading' : '';
		const accordionBodyClass = loading ? 'is-hidden' : '';
		const { list, label, onUpdate, triggers } = this.props;
		let addDisabled = false;
		if (triggers && triggers.length < 2) {
			addDisabled = 'disabled';
		}
		const variableName =
			this.props.list[0].toUpperCase() + this.props.list.substring(1);

		return (
			<Accordion>
				<AccordionItem>
					<AccordionItemTitle>
						<h3>
							{label}
							<span className="is-pulled-right">
								{this.state.expanded && (
									<button
										className="button is-small add-variable"
										onClick={this.showForm}
										disabled={addDisabled}
									>
										+ ADD {this.props.type.toUpperCase()}
									</button>
								)}
								{!this.state.expanded && (
									<button
										className="button is-small cancel-variable"
										onClick={this.toggleExpand}
									>
										EDIT
									</button>
								)}

								{this.state.expanded && (
									<button
										className="button is-small cancel-variable"
										onClick={this.toggleExpand}
									>
										COLLAPSE
									</button>
								)}

								<FontAwesomeIcon
									icon={arrowIcon}
									className="chevron-down"
									onClick={this.toggleExpand}
								/>
							</span>
						</h3>
					</AccordionItemTitle>
					<AccordionItemBody>
						<div
							className={`pageloader ${loading}`}
							style={{ position: 'relative' }}
						>
							<h3
								className={`button ${loading} accordion-loading-button`}
								style={{ left: '0%' }}
							>
								Loading
							</h3>
						</div>
						<div className={accordionBodyClass}>
							{addDisabled && (
								<div className="no-variables no-triggers">
									<p>
										<FontAwesomeIcon
											icon="info-circle"
											size="2x"
											color="#ff6238"
										/>
										You need to add a Trigger before adding an Event
									</p>
								</div>
							)}
							<div className={`modal ${showDeleteModal} delete-variable`}>
								<div className="modal-background" />
								<div className="modal-content">
									<div className="modal-header">
										<div className="columns">
											<div className="column is-1">
												<FontAwesomeIcon
													icon="exclamation-triangle"
													size="2x"
													color="red"
												/>
											</div>
											<div className="column">
												<strong>
													Are you sure you want to delete{' '}
													{this.state.deleteVariableName}?
												</strong>
												<br />
												<span>You will not be able to undo this operation</span>
											</div>
										</div>
									</div>
									<div className="is-pulled-right">
										<button
											className="button is-primary"
											onClick={this.closeDelete}
										>
											No
										</button>
										<button
											className="button is-danger"
											onClick={this.submitDelete}
										>
											Yes
										</button>
									</div>
									<button
										className="modal-close is-large"
										aria-label="close"
										onClick={this.closeDelete}
									/>
								</div>
							</div>
							{this.state.noVariables && (
								<div className="no-variables">
									<p>There are no {list}s added</p>
									<button
										className="button"
										onClick={this.showForm}
										disabled={addDisabled}
									>
										+ ADD {list.toUpperCase()}
									</button>
								</div>
							)}
							{this.state.editModalActive && (
								<EditModal
									closeEditModal={this.closeEditModal}
									variableName={variableName}
								>
									<div className="edit-modal">
										{this.props.children}
										<button className="button" onClick={this.closeEditModal}>
											Cancel
										</button>
										<button
											className="button is-primary"
											onClick={async () => {
												const res = await this.props.onUpdate();
												if (typeof res !== 'object') {
													//there are no errors throwing, i.e, submission success. Close the form.
													this.closeEditModal();
												}
											}}
										>
											Save
										</button>
									</div>
								</EditModal>
							)}

							{this.state.showForm && (
								<div className="new-variable-form">
									{this.props.children}
									<button className="button" onClick={this.closeForm}>
										Cancel
									</button>
									<button
										className="button is-primary"
										onClick={async () => {
											const res = await this.props.onUpdate();
											if (typeof res !== 'object') {
												this.closeForm();
											}
										}}
									>
										Save
									</button>
									<hr />
								</div>
							)}

							{this.state.variablesExist && (
								<div>
									<h3
										className="is-link margin-bottom-10px"
										style={{ cursor: 'text' }}
									>
										Saved {variableName + 's'}
									</h3>
									{this.renderVariableList(this.props.state[list + 'List'])}
								</div>
							)}
						</div>
					</AccordionItemBody>
				</AccordionItem>
			</Accordion>
		);
	}
}

const mapDispatchToProps = dispatch => ({
	updateClause: values => dispatch(updateClause(values)),
	updateVariableFormValues: values =>
		dispatch(updateVariableFormValues(values)),
	clearFormValues: formName => dispatch(clearFormValues(formName))
});

const mapStateToProps = state => {
	return { state };
};

export default connect(
	mapStateToProps,
	mapDispatchToProps
)(AccordionSection);



