import React, { Component } from 'react';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';

class TemplateList extends Component {
	state = {
		showDeleteModal: false,
		deleteVariableId: '',
		deleteVariableName: ''
	};

	openDelete = (id, name) => {
		this.setState({
			deleteVariableId: id,
			deleteVariableName: name,
			showDeleteModal: true
		});
	};

	closeDelete = () => {
		this.setState({ showDeleteModal: false });
	};

	submitDelete = () => {
		this.props.onDeleteClick(this.state.deleteVariableId).then(() => {
			this.setState({
				showDeleteModal: false
			});
		});
	};

	render() {
		const props = this.props;
		const showDeleteModal = this.state.showDeleteModal ? 'is-active' : '';
		return (
			<div className="template-list-wrapper box">
				<div className="template-list-header">
					<h2>
						Templates
						<button
							className="button new-template"
							onClick={props.onNewTemplateClick}
						>
							+ NEW TEMPLATE
						</button>
					</h2>
				</div>
				<table className="table is-fullwidth">
					<thead>
						<tr>
							<th>&nbsp;</th>
							<th>TITLE</th>
							<th>TYPE</th>
							<th>CREATED ON</th>
							<th>MODIFIED ON</th>
							<th>&nbsp;</th>
						</tr>
					</thead>
					<tbody>
						{props.templateList &&
							props.templateList.map(singleTemplate => {
								return (
									<tr key={singleTemplate.id}>
										<td>&nbsp;</td>
										<td>{singleTemplate.name}</td>
										<td className="template-title">{singleTemplate.model}</td>
										<td>{new Date(singleTemplate.createdAt).toDateString()}</td>
										<td>{new Date(singleTemplate.updatedAt).toDateString()}</td>
										<td>
											<div className="dropdown is-hoverable">
												<div className="dropdown-trigger">
													<FontAwesomeIcon
														icon="ellipsis-h"
														aria-haspopup="true"
														aria-controls="dropdown-menu2"
													/>
												</div>
												<div
													className="dropdown-menu"
													id="dropdown-menu2"
													role="menu"
												>
													<div className="dropdown-content">
														<a
															className="dropdown-item"
															onClick={() =>
																props.onModifyClick(singleTemplate.id)
															}
														>
															Modify
														</a>
														<hr className="dropdown-divider" />
														<a
															className="dropdown-item"
															onClick={() =>
																this.openDelete(
																	singleTemplate.id,
																	singleTemplate.name
																)
															}
														>
															Delete
														</a>
													</div>
												</div>
											</div>
										</td>
									</tr>
								);
							})}
					</tbody>
				</table>
				<div className={`modal ${showDeleteModal} delete-variable`}>
					<div className="modal-background" />
					<div className="modal-content">
						<div className="modal-header">
							<div className="columns">
								<div className="column is-1">
									<FontAwesomeIcon
										icon="exclamation-triangle"
										size="2x"
										color="red"
									/>
								</div>
								<div className="column">
									<strong>
										Are you sure you want to delete{' '}
										{this.state.deleteVariableName}?
									</strong>
									<br />
									<span>You will not be able to undo this operation</span>
								</div>
							</div>
						</div>
						<div className="is-pulled-right">
							<button className="button is-primary" onClick={this.closeDelete}>
								No
							</button>
							<button className="button is-danger" onClick={this.submitDelete}>
								Yes
							</button>
						</div>
						<button
							className="modal-close is-large"
							aria-label="close"
							onClick={this.closeDelete}
						/>
					</div>
				</div>
			</div>
		);
	}
}

export default TemplateList;

