import React from 'react';
import { reduxForm, Field, reset } from 'redux-form';
import { connect } from 'react-redux';

import RenderField from '../../FormElements/RenderField';
import logicalFields from '../logicalFields';
import { required } from '../../../validations';
import {
	getEvent,
	getTrigger,
	updateEvent,
	deleteEvent
} from '../../../actions';

import Accordion from './Accordion';

let Variable = props => {
	const logicalSection = logicalFields[2];
	const isUpdate =
		Object.keys(props.initialValues).length === 0 &&
		props.initialValues.constructor === Object
			? false
			: true;
	const options = [
		'Choose available Triggers',
		...props.state.triggerList
			.filter(trigger => trigger.template_id === props.state.activeTemplateId)
			.map(mappedTrigger => mappedTrigger.name)
	];
	return (
		<Accordion
			{...props}
			index={0}
			list="event"
			label={logicalSection.label}
			type={logicalSection.type}
			onAddVariableClick={() =>
				props
					.getEvent(props.state.activeTemplateId)
					.then(() => props.getTrigger())
			}
			deleteVariable={props.deleteEvent}
			onUpdate={props.handleSubmit(async values => {
				values.template_id = props.state.activeTemplateId;
				values.user_id = props.state.userId;
				values.trigger_id = props.state.triggerList.filter(
					a => a.name === values.trigger
				)[0].id;
				const res = props.updateEvent(values, isUpdate);
				return res;
			})}
			triggers={options}
		>
			<form
				onSubmit={props.handleSubmit(values => {
					values.template_id = props.state.activeTemplateId;
					values.user_id = props.state.userId;
					values.trigger_id = props.state.triggerList.filter(
						a => a.name === values.trigger
					)[0].id;
					props.updateEvent(values, isUpdate);
				})}
			>
				{logicalSection.inputs.map(singleField => (
					<Field
						key={singleField.name}
						component={RenderField}
						type={singleField.type}
						name={singleField.name}
						label={singleField.label}
						placeHolder={singleField.placeHolder}
						validate={required}
					/>
				))}
				<Field
					key={'asdf'}
					component={RenderField}
					type="select"
					name="trigger"
					label="Select a Trigger"
					placeHolder="Select a Trigger"
					validate={required}
					selectOptions={options}
				/>
				{/*<button className="button is-primary">Submit</button>*/}
			</form>
		</Accordion>
	);
};

const clearValues = (result, dispatch) => dispatch(reset('eventForm'));

Variable = reduxForm({
	form: 'eventForm',
	enableReinitialize: true,
	destroyOnUnmount: true,
	onSubmitSuccess: clearValues
})(Variable);

const mapDispatchToProps = dispatch => ({
	updateEvent: (values, isUpdate) => dispatch(updateEvent(values, isUpdate)),
	getEvent: tid => dispatch(getEvent(tid)),
	deleteEvent: (id, userId) => dispatch(deleteEvent(id, userId)),
	clearValues: () => dispatch(reset('eventForm')),
	getTrigger: () => dispatch(getTrigger())
});

//const mapStateToProps = state => ({ state });

const mapStateToProps = state => {
	const initialValues = state.variableFormValues || {};
	return { initialValues, state: state };
};

export default connect(
	mapStateToProps,
	mapDispatchToProps
)(Variable);



