import React, { Component } from 'react';
import { NavLink, Link } from 'react-router-dom';
import { connect } from 'react-redux';
import { saveTemplateName } from '../../actions';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';

class HeaderTab extends Component {
	state = {
		isEditable: false,
		templateName: '',
		templateNamePrevious: '',
		templateModel: '',
		currentTemplateIndex: null
	};
	makeEditable = () => {
		this.setState({ isEditable: true });
	};
	closeEditable = () => {
		this.setState({
			templateName: this.state.templateNamePrevious,
			isEditable: false
		});
	};
	saveTemplateName = () => {
		const updatedTemplatesList = this.props.templates;
		const values = {
			name: this.state.templateName,
			model: this.state.templateModel,
			user_id: this.props.userId,
			updatedAt: new Date().toISOString()
		};
		let updatedTemplate = this.props.templates[this.state.currentTemplateIndex];
		updatedTemplate = { ...updatedTemplate, ...values };
		updatedTemplatesList[this.state.currentTemplateIndex] = updatedTemplate;

		this.props
			.saveTemplateName(
				values,
				this.props.activeTemplateId,
				updatedTemplatesList
			)
			.then(() =>
				this.setState({
					isEditable: false
				})
			);
	};
	handleChange = e => {
		this.setState({
			templateName: e.target.value
		});
	};

	componentDidMount() {
		const props = this.props;
		const templateData =
			props.templates.filter(({ id }) => id === props.activeTemplateId)[0] ||
			{};
		const index = props.templates.indexOf(templateData);

		this.setState({
			templateName: templateData.name,
			templateNamePrevious: templateData.name,
			templateModel: templateData.model,
			currentTemplateIndex: index
		});
	}
	render() {
		return (
			<div className="box header-tab">
				<nav
					className="breadcrumb has-succeeds-separator"
					aria-label="breadcrumbs"
				>
					<ul>
						<li>
							<Link to="/template">Templates</Link>
						</li>
						<li className="is-active">
							<a aria-current="page">{this.props.activeTab}</a>
						</li>
					</ul>
				</nav>
				<hr className="hr" />
				<div>
					<strong className="is-flex edit-template-name-wrapper">
						<input
							className="edit-template-name"
							disabled={!this.state.isEditable}
							onChange={this.handleChange}
							value={this.state.templateName}
						/>
						{this.state.isEditable ? (
							<span>
								<button
									className="button is-primary"
									onClick={this.saveTemplateName}
									disabled={!this.state.templateName}
								>
									Save
								</button>
								<button className="button" onClick={this.closeEditable}>
									Cancel
								</button>
							</span>
						) : (
							<FontAwesomeIcon
								icon="pen"
								color="#00B79B"
								onClick={this.makeEditable}
							/>
						)}
					</strong>
					<p>
						This section is used to create a template using the two sections
						provided below, one being the place where user enters the entire
						content and other being technical information that needs to be
						included in the template
					</p>
				</div>
				<hr className="hr" />

				<div className="tabs">
					<ul>
						<li>
							<NavLink activeClassName="is-active" to="/template/textual">
								TEXTUAL
							</NavLink>
						</li>
						<li>
							<NavLink activeClassName="is-active" to="/template/logical">
								LOGICAL
							</NavLink>
						</li>
					</ul>
				</div>
				<div className="cancel-next">
					<Link to="/template" className="button">
						CANCEL
					</Link>
					<Link to={'logical'} className="button is-primary">
						NEXT
					</Link>
				</div>
			</div>
		);
	}
}

const mapStateToProps = state => state;

const mapDispatchToProps = dispatch => ({
	saveTemplateName: (values, templateId, updatedTemplatesList) =>
		dispatch(saveTemplateName(values, templateId, updatedTemplatesList))
});

export default connect(
	mapStateToProps,
	mapDispatchToProps
)(HeaderTab);



