import React, { Component } from 'react';
import { reduxForm, Field } from 'redux-form';
import { connect } from 'react-redux';
import { withRouter } from 'react-router-dom';

import RenderField from '../FormElements/RenderField';
import { required } from '../../validations';
import textualFields from './textualFields';
import HeaderTab from './HeaderTab';
import { updateTextualData } from '../../actions';

class Textual extends Component {
	componentDidMount() {
		if (!this.props.state.activeTemplateId) {
			this.props.history.push('/template');
		}
	}
	render() {
		return (
			<div>
				<HeaderTab activeTab="Textual" />
				<form
					className="box"
					onSubmit={this.props.handleSubmit(values => {
						values.user_id = this.props.state.userId;
						values.template_id = this.props.state.activeTemplateId;
						//values.template_textual_id = this.props.state.activeTemplateId;
						return this.props.updateTextualData(values);
					})}
				>
					<h2>Textual</h2>
					{textualFields.map(field => (
						<Field
							key={field.name}
							type="textarea"
							component={RenderField}
							name={field.name}
							label={field.label}
							placeHolder={field.placeHolder}
							validate={required}
						/>
					))}
					<button className="button">Save</button>
					<br />
				</form>
			</div>
		);
	}
}

const mapDispatchToProps = dispatch => ({
	updateTextualData: values => dispatch(updateTextualData(values))
});

const mapStateToProps = state => {
	return {
		initialValues: state.activeTextualData,
		state: state
	};
};

Textual = reduxForm({
	form: 'textualTemplate',
	enableReinitialize: true,
	destroyOnUnmount: true
})(Textual);

export default withRouter(
	connect(
		mapStateToProps,
		mapDispatchToProps
	)(Textual)
);



