import React from 'react';
import RenderField from '../FormElements/RenderField';
import { reduxForm, Field } from 'redux-form';
import { required } from '../../validations';

const selectOptions = [
	'Type of template',
	'Negotiation',
	'Lorem Ipsum',
	'Dolor sit',
	'Amet Consectueter',
	'necessitatibus eius'
];

const NewTemplate = props => {
	return (
		<div className="modal is-active new-template-modal">
			<div className="modal-background" onClick={props.closeModal} />
			<div className="modal-content">
				<p className="modal-header">
					<strong>Create a New template</strong>
				</p>
				<form
					onSubmit={props.handleSubmit(values => props.addTemplate(values))}
					className="new-template-field"
				>
					<Field
						key="0"
						type="input"
						component={RenderField}
						name={'name'}
						label={'Name of Template'}
						placeHolder={'Enter name of the template'}
						validate={required}
						inputField={true}
					/>
					<Field
						key="2"
						type="select"
						component={RenderField}
						selectOptions={selectOptions}
						name={'model'}
						label={'Type of Template'}
						placeHolder={'Enter name of the template'}
						validate={required}
						inputField={true}
					/>
					<hr />
					<span className="is-pulled-right">
						<button type="button" className="button" onClick={props.closeModal}>
							CANCEL
						</button>&nbsp;
						<button type="submit" className="button proceed">
							PROCEED
						</button>
					</span>
					<br />
				</form>
			</div>
			<button
				className="modal-close is-large"
				aria-label="close"
				onClick={props.closeModal}
			/>
		</div>
	);
};

export default reduxForm({
	form: 'newTemplate'
})(NewTemplate);



