import React, { Component } from 'react';
import { Route } from 'react-router-dom';
import axios from 'axios';
import { connect } from 'react-redux';
import Textual from './Textual';
import Logical from './Logical';
import TemplateList from './TemplateList';
import NoTemplates from './NoTemplates';
import NewTemplate from './NewTemplate';
//import HeaderTab from './HeaderTab';
import { changeActiveTemplateId, getTextualData } from '../../actions';
import { withRouter } from 'react-router-dom';

class Template extends Component {
  state = {
    active: 'list',
    newTemplateActive: false,
    modalActive: false
  };

  closeModal = () => {
    this.setState({ newTemplateActive: false });
  };

  openModal = () => {
    this.setState({ newTemplateActive: true });
  };

  handleModifyClick = id => {
    this.props.changeActiveTemplateId(id);
    this.props.getTextualData(id);
    this.props.history.push('/template/textual');
  };

  getTextualData = () => {
    axios
      .get('/api/templates/edit/textual/4')
      .then(response => console.log(response));
  };

  getLogicalData = () => {
    axios
      .get('/api/templates/edit/logical/2')
      .then(response => console.log(response));
  };

  deleteTemplateQ = e => {
    console.log(e);
  };

  handleOnSuccess = () => {
    this.setState({ active: 'textual' });
  };

  handleNewTemplateClick = () => {
    this.setState({ active: 'newTemplate' });
  };

  addTemplate = async values => {
    values.user_id = this.props.userId;
    const id = await this.props.addTemplate(values);
    await this.closeModal();
    await this.props.changeActiveTemplateId(id);
    await this.props.getTextualData(id);
    this.props.history.push('/template/textual');
  };

  renderTab = () => {
    if (this.state.active === 'list')
      return (
        <TemplateList
          {...this.props}
          onModifyClick={this.handleModifyClick}
          onDeleteClick={this.props.deleteTemplate}
          onNewTemplateClick={this.handleNewTemplateClick}
        />
      );
    if (this.state.active === 'textual') return <Textual />;
    if (this.state.active === 'newTemplate')
      return (
        <NewTemplate
          addTemplate={this.props.addTemplate}
          onSuccess={this.handleOnSuccess}
        />
      );
    return <Logical />;
  };

  render() {
    return (
      <div>
        {/*<Link to={`${this.props.match.url}/textual`}>Goto Textual</Link>
        <Link to={`${this.props.match.url}/logical`}>Goto Logical</Link>*/}

        <br />
        {/*<button onClick={this.getLogicalData}>Get Logical Data</button>
        <button onClick={this.getTextualData}>Get Textual Data</button>
        <button
          onClick={() => {
            this.props.history.push('/template/textual');
          }}
        >
          Textual
        </button>
        <button
          onClick={() => {
            this.props.history.push('/template/logical');
          }}
        >
          Logical
        </button>
        <button
          onClick={() => {
            this.setState({ active: 'list' });
            this.props.history.push('/template');
          }}
        >
          List
        </button>
        <button
          onClick={() => {
            //this.props.history.push('/template/new');
            this.openModal();
          }}
        >
          New Template
        </button>*/}

        <Route
          exact
          path="/template"
          render={() =>
            this.props.templates.length ? (
              <TemplateList
                {...this.props}
                onModifyClick={this.handleModifyClick}
                onDeleteClick={this.props.deleteTemplate}
                onNewTemplateClick={this.openModal}
              />
            ) : (
              <NoTemplates openModal={this.openModal} />
            )
          }
        />
        <Route path={`${this.props.match.url}/textual`} component={Textual} />
        <Route
          path={`${this.props.match.url}/logical`}
          render={() => (
            <Logical
              activeUserId={this.props.activeUserId}
              activeTemplateId={this.props.activeTemplateId}
            />
          )}
        />
        {this.state.newTemplateActive && (
          <NewTemplate
            addTemplate={this.addTemplate}
            active={this.state.newTemplateActive}
            closeModal={this.closeModal}
          />
        )}
      </div>
    );
  }
}

const mapStateToProps = state => {
  window.state = state;
  return state;
};

const mapDispatchToProps = dispatch => ({
  changeActiveTemplateId: id => dispatch(changeActiveTemplateId(id)),
  getTextualData: id => dispatch(getTextualData(id))
});

export default withRouter(
  connect(
    mapStateToProps,
    mapDispatchToProps
  )(Template)
);



