//remove this in production
window.axios = axios;

export const simpleAction = data => dispatch => {
	dispatch({
		type: 'SIMPLE_ACTION',
		payload: data
	});
};

export const clearFormValues = formName => dispatch => {
	dispatch({
		type: `FETCH_VARIABLE_FORM_VALUES`,
		payload: null
	});
};

export const saveTemplateName = (
	values,
	templateId,
	updatedTemplatesList
) => async dispatch => {
	const { data } = await axios.put(`/api/templates/${templateId}`, values);
	if (!data.error) {
		alert('Updated successfully!');
		dispatch({
			type: 'FETCH_TEMPLATES',
			payload: updatedTemplatesList
		});
	}
};

export const updateUserId = id => dispatch => {
	dispatch({
		type: 'FETCH_USERID',
		payload: id
	});
};

export const changeActiveTemplateId = id => dispatch => {
	dispatch({
		type: 'CHANGE_ACTIVE_TEMPLATE',
		payload: id
	});
};

export const fetchTemplates = data => async dispatch => {
	const { data } = await axios.get('/api/templates');
	dispatch({
		type: 'FETCH_TEMPLATES',
		payload: data.data
	});
};

export const deleteTemplate = id => async dispatch => {
	const { data } = await axios.delete(`/api/templates/${id}/2`);
	if (!data.error) {
		dispatch({
			type: 'DELETE_TEMPLATE',
			payload: id
		});
		alert('Template has been deleted');
	}
};

export const addTemplate = (values, user_id) => async dispatch => {
	const { data } = await axios.post('/api/templates', values);
	if (!data.error) {
		dispatch({
			type: 'ADD_TEMPLATE',
			payload: data.data
		});
		alert('Template has been added');
		return data.data.id;
	}
	return false;
};

export const updateTextualData = values => async dispatch => {
	delete values.id;
	delete values.createdAt;
	delete values.updatedAt;
	const { data } = await axios.post('/api/templates/edit/textual', values);
	if (!data.error) {
		alert('Textual data has been updated');
	}
	const getData = await axios.get(
		`/api/templates/edit/textual/${values.template_id}`
	);
};

export const getTextualData = id => async dispatch => {
	startPageLoader(dispatch);
	const { data } = await axios.get(`/api/templates/edit/textual/${id}`);
	if (!data.error) {
		dispatch({
			type: 'CHANGE_ACTIVE_TEXTUAL_DATA',
			payload: data.data
		});
	}
	stopPageLoader(dispatch);
};

export const updateVariable = (values, isUpdate) => async dispatch => {
	if (isUpdate) {
		const { data } = await axios.put(`/api/variables/${values.id}`, values);
		if (!data.error) {
			dispatch({
				type: 'UPDATE_TO_VARIABLE_LIST',
				payload: values
			});
		}
		alert('Variable updated successfully');
		return;
	}
	const { data } = await axios.post('/api/variables/', values);
	if (!data.error) {
		dispatch({
			type: 'ADD_TO_VARIABLE_LIST',
			payload: data.data
		});
	}
	alert('Variable has been added');
};

export const deleteVariable = (id, userId) => async dispatch => {
	const { data } = await axios.delete(`/api/variables/${id}/${userId}`);
	if (!data.error) {
		dispatch({
			type: 'REMOVE_FROM_VARIABLE_LIST',
			payload: id
		});
	}
	alert('Variable has been deleted');
};

