export const updateClause = (values, isUpdate) => async dispatch => {
	if (isUpdate) {
		const { data } = await axios.put(`/api/clauselogic/${values.id}`, values);
		if (!data.error) {
			dispatch({
				type: 'UPDATE_TO_CLAUSE_LIST',
				payload: values
			});
		}
		alert('Clause updated successfully');
		return;
	}

	const { data } = await axios.post('/api/clauselogic/', values);
	if (!data.error) {
		dispatch({
			type: 'ADD_TO_CLAUSE_LIST',
			payload: data.data
		});
	}
	alert('Clause has been added');
};

